body {
  letter-spacing: .02em;
  font-family: Roboto, sans-serif;
}

.loader {
  color: #000;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: bold;
  display: none;
  position: relative;
}

.loader:before {
  content: "";
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #5029b9;
  border-radius: 50%;
  animation: rotation .6s linear infinite;
  position: absolute;
  bottom: 8px;
  left: 30px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: red;
  display: none;
}

.cat-info {
  display: block;
}

